rn{\r\n            loading: true,\r\n            albumInfo : null,\r\n            images:[],\r\n            imageIndex: null,\r\n        }\r\n    },\r\n    created(){\r\n        this.getAlbumData();\r\n    },\r\n    methods:{\r\n        getAlbumData(){\r\n            axios.get(\"/api/album-with-image\",{\r\n                params:{\r\n                    albumid: this.$route.params.id\r\n                }\r\n            }).then(resp =>{\r\n                return resp.data;\r\n            }).then(data =>{\r\n                this.albumInfo = data.albumInfo;\r\n                this.images = data.images;\r\n                this.loading = false;\r\n            }).catch(err=>{\r\n                console.error(err.response.data);\r\n            })\r\n        },\r\n        hideGallery(){\r\n            this.imageIndex = null;\r\n            $('.cool-lightbox').hide();\r\n        }\r\n    }\r\n}\r\n</script>\r\n\r\n<style>\r\n.single-gallery-img{\r\n    width: 100%;\r\n    height: 250px;\r\n    object-fit: cover;\r\n    box-shadow: 0 3px 13px 4px rgba(0, 0, 0, 0.14);\r\n    transition: 0.4s;\r\n}\r\n.single-gallery-img:hover{\r\n    opacity: 50%;\r\n}\r\n.cool-lightbox-caption h6{\r\n    color: white !important;\r\n}\r\n</style>"],"sourceRoot":""}]);
