import ___CSS_LOADER_API_SOURCEMAP_IMPORT___ from "../../../../../../node_modules/css-loader/dist/runtime/cssWithMappingToString.js";
import ___CSS_LOADER_API_IMPORT___ from "../../../../../../node_modules/css-loader/dist/runtime/api.js";
var ___CSS_LOADER_EXPORT___ = ___CSS_LOADER_API_IMPORT___(___CSS_LOADER_API_SOURCEMAP_IMPORT___);
// Module
___CSS_LOADER_EXPORT___.push([module.id, ".album-box img{width:100%;height:200px;-o-object-fit:cover;object-fit:cover;cursor:pointer}.select-box{width:100%;padding:15px 0;cursor:pointer}", "",{"version":3,"sources":["webpack://./resources/js/components/admin/pages/Gallery/Details.vue"],"names":[],"mappings":"AAiKA,eACA,UAAA,CACA,YAAA,CACA,mBAAA,CAAA,gBAAA,CACA,cACA,CACA,YACA,UAAA,CACA,cAAA,CACA,cACA","sourcesContent":["<template>\r\n  <section class=\"section\">\r\n      <div class=\"row justify-content-center\">\r\n          <div class=\"container-fluid\">\r\n              <div class=\"col-md-12 col-lg-12\">\r\n                <div class=\"card\">\r\n                    <div class=\"card-header d-flex justify-content-between\">\r\n                        <h4 v-cloak>{{ album_data.name }}</h4>\r\n                        <div class=\"text-right\">\r\n                            <router-link :to=\"{name:'admin-upload-album-photo', params:{id: this.$route.params.id}}\" class=\"btn btn-sm btn-primary\"><i class=\"fas fa-plus\"></i> Add Image</router-link>\r\n                            <button v-if=\"checked_images > 0\"\r\n                                 class=\"btn btn-sm btn-danger\" \r\n                                 @click=\"removeSelectedImage\"\r\n                                 title=\"Delete Selected\"><i class=\"fas fa-trash\"></i> {{ checked_images }} Items</button>\r\n                            <button v-if=\"images.length>0\" class=\"btn btn-sm btn-danger\" @click=\"emptyAlbum\">Empty Album</button>\r\n                        </div>\r\n                    </div>\r\n                    <div class=\"card-body\">\r\n                        <div class=\"row\" v-if=\"images.length>0\">\r\n\r\n                            <div class=\"col-md-2 col-sm-3 col-6\" v-for=\"(image,i) in images\" :key=\"i\">\r\n                                <div class=\"album-box\">\r\n                                    <img :src=\"`/upload/album/${image.album.name}/${image.image}`\" :alt=\"image.title\">\r\n                                    <div>\r\n                                        <label class=\"select-box\">\r\n                                            <input type=\"checkbox\" name=\"img\" @change=\"multiSelect\" :value=\"image.id\">\r\n                                        </label>\r\n                                    </div>\r\n                                </div>\r\n                            </div>\r\n\r\n                        </div>\r\n                        <div class=\"row justify-content-center\" v-else>\r\n                            <div class=\"col-12 col-md-6\">\r\n                                <div class=\"alert alert-secondary text-center\" role=\"alert\">\r\n                                    0 Photos Found\r\n                                </div>\r\n                            </div>\r\n                        </div>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n          </div>\r\n      </div>\r\n  </section>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n    data(){\r\n        return{\r\n            album_data: [],\r\n            images: [],\r\n            checked_images : 0,\r\n            selected_image : [],\r\n        }\r\n    },\r\n    created(){\r\n        this.getAlbumData();\r\n        this.getAllImages();\r\n    },\r\n    methods:{\r\n        getAlbumData(){\r\n            axios.get(\"/admin/get-album-data\",{\r\n                params:{\r\n                    albumId : this.$route.params.id\r\n                }\r\n            }).then((resp)=>{\r\n                return resp.data;\r\n            }).then((data)=>{\r\n                this.album_data = data;\r\n            }).catch((err)=>{\r\n                this.$router.push({name: 'admin-photo-album'});\r\n                console.error(err.response.data);\r\n            })\r\n        },\r\n        getAllImages(){\r\n            axios.get(\"/admin/album-images\",{\r\n                params:{\r\n                    albumId:this.$route.params.id\r\n                }\r\n            }).then((resp)=>{\r\n                return resp.data;\r\n            }).then((data)=>{\r\n                this.images = data;\r\n            }).catch((err)=>{\r\n                console.error(err.response.data);\r\n            })\r\n        },\r\n        multiSelect(){\r\n            this.checked_images = $(\"input[type='checkbox'][name='img']:checked\").length;\r\n            var arr = [];\r\n            let i = 0;\r\n            $(\"input[type='checkbox'][name='img']:checked\").each(function(){\r\n                arr[i++] = $(this).val();\r\n            });\r\n            this.selected_image = arr;\r\n        },\r\n        removeSelectedImage(){\r\n        \r\n            Swal.fire({\r\n                title: `Are you sure you want to delete this ${this.checked_images} items`,\r\n                text: \"Once deleted, can't be restored!!\",\r\n                icon: \"question\",\r\n                showCancelButton: true,\r\n                confirmButtonColor: '#d33',\r\n                cancelButtonColor: '#73e375',\r\n                confirmButtonText: 'Delete'\r\n            }).then((data)=>{\r\n                if(data.isConfirmed){\r\n                    \r\n                    axios.post(\"/admin/delete-album-images\",{\r\n                        imgs: this.selected_image\r\n                    }).then((resp)=>{\r\n                        return resp.data;\r\n                    }).then((data)=>{\r\n                        if(data.status == \"OK\")\r\n                        {\r\n                            $(\"input[type='checkbox'][name='img']:checked\").parent().parent().parent().parent().remove();\r\n                            this.checked_images = 0;\r\n                            this.selected_image = [];\r\n                        }\r\n                    }).catch((err)=>{\r\n                        console.error(err.response.data);\r\n                    });\r\n\r\n\r\n                }\r\n            });\r\n        },\r\n        emptyAlbum(){\r\n            \r\n            Swal.fire({\r\n                title: `Are you sure you want to empty this album`,\r\n                text: \"Once deleted, can't be restored!!\",\r\n                icon: \"question\",\r\n                showCancelButton: true,\r\n                confirmButtonColor: '#d33',\r\n                cancelButtonColor: '#73e375',\r\n                confirmButtonText: 'Delete'\r\n            }).then((data)=>{\r\n                if(data.isConfirmed){\r\n                    axios.post(\"/admin/clear-album\",{\r\n                        albumId: this.$route.params.id,\r\n                    }).then(resp =>{\r\n                        return resp.data;\r\n                    }).then(data =>{\r\n                        if(data.status == \"OK\"){\r\n                            window.location.reload();\r\n                        }\r\n                    }).catch(err =>{\r\n                        console.error(err.response.data);\r\n                    });\r\n                }\r\n            });\r\n        }\r\n    }\r\n}\r\n</script>\r\n\r\n<style>\r\n.album-box img{\r\n    width: 100%;\r\n    height: 200px;\r\n    object-fit: cover;\r\n    cursor: pointer;\r\n}\r\n.select-box{\r\n    width: 100%;\r\n    padding: 15px 0;\r\n    cursor: pointer;\r\n}\r\n</style>"],"sourceRoot":""}]);
